import React, { Component, Suspense } from 'react';
import DisplayCode from '../../../PublishCommonComponents/DisplayCode';
import { UGC_SUITE_WALL_PREFIX } from '../../../../../Services/Api'

const AptoideCode = ({ data }) => {
    return <div className="card text-decoration-none">
        <div className="card-body p-lg-7">
            <div className="m-auto">
                <div className="embed_code">
                    <h5 className="mb-5 fw-bolder">To Display this Tagbox display wall with Aptoide Tv app Follow Given Steps</h5>
                    <h6>Step 1 : Collect The Generated Social Wall Display Code</h6>
                    <DisplayCode displayCode={data && Object.keys(data).length > 0 ? `${UGC_SUITE_WALL_PREFIX}${data.wallId}` : null} />
                    <h6 className="mb-5">Step 2 : Display Social wall with Aptoide TV</h6>
                    <ul className="list-unstyled list-points fs-7">
                        <li className="mb-3">Go to your Aptoide tv store and search for Tagbox Display</li>
                        <li className="mb-3">Install the Tagbox Display app and launch it</li>
                        <li className="mb-3">Next, enter the collected display code in the app</li>
                        <li className="mb-3">Done! Youâ€™re all set to showcase your social wall.</li>
                    </ul>
                </div>
            </div>
        </div>
    </div>
}

export default AptoideCode;
